.yot-hamburger span {
  display: block;
  width: 30px;
  height: 3px;
  background-color: hsl(0deg, 0%, 100%);
  margin-bottom: 7px;
  transition: all 300ms ease-in-out;
  transform-origin: left center;
}
.yot-hamburger span:last-child {
  margin-bottom: 0;
}

.yot-hamburger-open span:first-child {
  transform: rotate(45deg);
}
.yot-hamburger-open span:nth-child(2) {
  opacity: 0;
}
.yot-hamburger-open span:last-child {
  transform: rotate(-45deg);
}

